.advantages__wrapper {
  padding-top: 37px;
  padding-bottom: 32px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .advantages__wrapper {
    padding-top: 47px;
    padding-bottom: 55px;
  }
}

@media (min-width: 1200px) {
  .advantages__wrapper {
    padding-top: 59px;
    padding-bottom: 44px;
  }
}

.advantages__header {
  font-size: 18px;
  line-height: 26px;
  font-weight: 900;
  text-transform: uppercase;
  text-align: center;
}

@media (min-width: 768px){
  .advantages__header {
    font-size: 21px;
    line-height: 26px;
  }
}
.advantages__causes {
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  text-transform: uppercase;
  text-align: center;
  display: block;
}

@media(min-width:768px) {
  .advantages__causes--home {
    grid-column-start: 2;
    grid-column-end: 4;
  }
}

@media (min-width: 768px) {
  .advantages__causes--devil {
    grid-column-start: 1;
    grid-column-end: 1;
  }
}

@media (min-width: 768px) {
  .advantages__item--column {
    grid-column-start: 3;
    grid-column-end: 4;
  }
}

  @media (min-width: 768px) {
  .advantages__list {
  display: grid;
  grid-template-columns: repeat(3, 256px);
  grid-template-rows: repeat(4, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  }
  }

  @media (min-width: 1200px) {
    .advantages__list {
      grid-template-columns: repeat(3, 400px);
    }
  }


.advantages__item {
  background-color: #81b3d2;
  text-decoration: none;
  display: block;
}

.advantages__item--white {
  background-color:#eeeeee;
}

.advantages__title {
  font-size: 18px;
  line-height: 26px;
  font-weight: 900;
  text-transform: uppercase;
}

.advantages__title--black{
  color: #000000;
}

.advantages__description {
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  text-transform: uppercase;
}

.advantages__description--black{
  color: #000000;
}

.advantages__pross-title {
  color: #000000;
  font-size: 18px;
  line-height: 26px;
  font-weight: 900;
  text-transform: uppercase;
}

.advantages__pross-description {
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  text-transform: uppercase;
  color: #000000;
}

.advantages__pross-item {
  display: block;
  text-decoration: none;
}

.advantages__devil {
  background-image: url(../img/devil.jpg);
  width: 320px;
  height: 260px;
  background-repeat: no-repeat;
}

@media (min-width: 768px) {
  .advantages__devil {
    background-image:url(../img/devil-tablet.jpg);
    width: 512px;
    height: 256px;
    background-repeat: no-repeat;
  }
}

@media (min-width: 1200px) {
  .advantages__devil {
    background-image: url(../img/devil-desktop.jpg);
    width: 800px;
    height: 256px;
    background-repeat: no-repeat;
  }
}

.advantages__home {
background-image: url(../img/home.jpg);
background-repeat: no-repeat;
width: 320px;
height: 260px;
}

@media (min-width:768px) {
  .advantages__home {
    background-image:url(../img/home-tablet.jpg);
    background-repeat:no-repeat;
    width: 512px;
    height: 256px;
  }
}

@media (min-width: 1200px) {
  .advantages__home {
    background-image: url(../img/home-desktop.jpg);
    width: 800px;
    height: 256px;
    background-repeat: no-repeat;
  }
}
